import "./App.css";
import { Toaster } from "react-hot-toast";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";

import {
  create_file_route,
  dashboard_route,
  login_route,
  logout_route,
  register_route,
} from "./constants/routes";

import AuthContextProvider from "./context/AuthContextProvider";
import AppContextProvider from "./context/AppContextProvider";

import Login from "./components/Login";
import Register from "./components/Register";
import Dashboard from "./components/Dashboard";
import PrivateRoute from "./components/PrivateRoute";
import NotForLoggedInUser from "./components/NotForLoggedInUser";
import Layout from "./components/Layout";
import CreateFile from "./components/CreateFile";

const App = () => {
  return (
    <>
      <Router>
        <AuthContextProvider>
          <AppContextProvider>
            <Routes>
              <Route path="/" element={<Layout />}>
                <Route index element={<Navigate to={dashboard_route} />} />
                <Route
                  path={dashboard_route}
                  element={
                    <PrivateRoute>
                      <Dashboard />
                    </PrivateRoute>
                  }
                />
                <Route
                  path={create_file_route}
                  element={
                    <PrivateRoute>
                      <CreateFile />
                    </PrivateRoute>
                  }
                />
              </Route>

              <Route
                path={login_route}
                element={
                  <NotForLoggedInUser>
                    <Login />
                  </NotForLoggedInUser>
                }
              />
              <Route
                path={register_route}
                element={
                  <NotForLoggedInUser>
                    <Register />
                  </NotForLoggedInUser>
                }
              />
              <Route
                path={logout_route}
                element={<Navigate to={login_route} />}
              />
            </Routes>
          </AppContextProvider>
        </AuthContextProvider>
      </Router>
      <Toaster />
    </>
  );
};

export default App;
