import "../styles/form.css";
import { useForm } from "react-hook-form";
import { useAppContext } from "../context/AppContextProvider";

const CreateFile = () => {
  const { createFile } = useAppContext();

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = (data) => {
    const fd = new FormData();
    fd.append("file", data.file[0]);
    fd.append("label", data.label);
    fd.append("title", data.title);

    createFile(fd);
  };

  return (
    <div className="wrapper">
      <div className="form-container">
        <div className="header">
          <h4>Importation du fichier csv / Json</h4>
        </div>
        <form
          className="form"
          onSubmit={handleSubmit((data) => {
            onSubmit(data);
          })}
        >
          <div className="form-control">
            <label htmlFor="file">File</label>
            <input
              className={errors.file ? "pointer error" : "pointer"}
              id="file"
              type="file"
              {...register("file", {
                required: "Fichier est requis",
                pattern: {
                  value: /^.*\.(csv|CSV|JSON|json)$/i,
                  message: "veuillez fournir un fichier json ou csv",
                },
                validate: (value) =>
                  value[0].name.includes(".csv") ||
                  value[0].name.includes(".json") ||
                  "veuillez fournir un fichier json ou csv",
              })}
            />
            {errors.file && <small>{errors.file.message}</small>}
          </div>
          <div className="form-control">
            <label htmlFor="title">Titre</label>
            <input
              className={errors.title && "error"}
              {...register("title", {
                required: "le titre est requis",
                minLength: {
                  value: 3,
                  message: "veuillez fournir au moins 3 caractères",
                },
              })}
              placeholder="Entrer le titre (eg. Population par ville)"
            />
            {errors.title && <small>{errors.title.message}</small>}
          </div>

          <div className="form-control">
            <label htmlFor="label">Label</label>
            <input
              className={errors.label && "error"}
              {...register("label", {
                required: "le label est requis",
                minLength: {
                  value: 3,
                  message: "veuillez fournir au moins 3 caractères",
                },
              })}
              placeholder="Entrer le label (eg. Ville)"
            />
            {errors.label && <small>{errors.label.message}</small>}
          </div>

          <button>Submit</button>
        </form>
      </div>
    </div>
  );
};

export default CreateFile;
