const Dashboard = () => {
  const [labels, setLabels] = useState([]);
  const [label, setLabel] = useState("");
  const [title, setTitle] = useState("");
  const [dataset, setDataset] = useState([]);
  const chartRef = useRef(null);

  const { fileNames, getFileNames, getData, file } = useAppContext();

  // const bgColor = generateRandomColor();

  const options = {
    responsive: true,

    plugins: {
      legend: {
        position: "bottom",
      },
      title: {
        display: true,
        text: title,
      },
    },
  };

  const data = {
    labels,
    datasets: [
      {
        label,
        data: dataset,
        backgroundColor: ["#303189", "#B22222", "#74992e", "#FF0000", "#FF8C00", "#A9A9A9", "#FFD700", "#F8F8FF", "#4B0082", "#ADD8E6", "#DDA0DD", "#FF0000"]// bgColor,
      },
    ],
  };
  const data2 = {
    labels,
    datasets: [
      {
        label,
        data: dataset,
        backgroundColor: "#303189"// bgColor,
      },
    ],
  };
  


  useEffect(() => {
    getFileNames();
  }, []);

  useEffect(() => {
    let labelsFromData = [];
    let datasetFromData = [];
    file.fileData?.map((fd) => {
      labelsFromData.push(fd.key);
      datasetFromData.push(fd.value);
    });
    setDataset(datasetFromData);
    setLabels(labelsFromData);
    setLabel(file.label);
    setTitle(file.title);
  }, [file]);

  const downloadChart = () => {
    const b64Data = chartRef.current.toBase64Image("image/jpg", 1);
    const a = document.createElement("a");
    document.body.appendChild(a);
    a.href = b64Data;
    a.download = "graph.jpg";
    a.click();
    document.body.removeChild(a);
  };

  if (!Object.keys(file).length < 0 || file.fileData?.length < 0)
    return <p>Loading</p>;

  return (
    <>
      <div className="dash-select-btn">
        <div className="dash-select">
          <Select
            defaultValue={fileNames[0]}
            onChange={(value) => getData(value.value)}
            options={fileNames}
          />
        </div>
        <button className="dash-btn pointer" onClick={downloadChart}>
          Télécharger
        </button>
      </div>
      <div
        className="dash-chart"
       
                //width: "clamp(30rem, 75%, 90rem)",
         // marginInline: "auto",
      
      >
        
        <Bar options={options} data={data2} ref={chartRef} />

        <Doughnut options={options} data={data} />
   
        <Pie options={options} data={data} />
      </div>
    </>
  );
  
};

export default Dashboard;
