import "../styles/navbar.css";
import { useState, useRef, useEffect } from "react";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";

import {
  create_file_route,
  dashboard_route,
  logout_route,
} from "../constants/routes";

import img from "../assets/profile/user.jpg";

import { useAuthContext } from "../context/AuthContextProvider";
import OutsideClick from "./OutsideClick";

const Navbar = () => {
  const navigate =useNavigate();
  const [open, setOpen] = useState(false);
  const { logout } = useAuthContext();

  const closeNavLinks = () => setOpen(false);

  return (
    <nav className="nav">
      {/* <OutsideClick onOutsideClick={closeNavLinks}> */}
 

        <div >


<button onClick={()=> {closeNavLinks(); navigate(dashboard_route)}}>
  Dashbord
</button>

<button onClick={()=> {closeNavLinks(); navigate(create_file_route)}}>
  Importer
</button>


<button onClick={()=> { logout(); navigate(logout_route)}}>
  Se déconnecter
</button>
{/* <Link to={logout_route} onClick={logout}>
            déconnecter
          </Link> */}

        </div>
      {/* </OutsideClick> */}
    </nav>
  );

};

export default Navbar;
