import "../styles/form.css";

import { useRef } from "react";

import { useForm } from "react-hook-form";
import { useAuthContext } from "../context/AuthContextProvider";
import { Link } from "react-router-dom";
import { login_route } from "../constants/routes";

const Register = () => {
  const { SignUp } = useAuthContext();

  const {
    register,
    watch,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const password = useRef();
  password.current = watch("password");

  const onSubmit = (data) => {
    delete data.cpassword;

    SignUp(data);
  };

  return (
    <div className="wrapper">
      <div className="form-container">
        <div className="header">
          <h4>S’inscrire</h4>
        </div>
        <form
          className="form"
          onSubmit={handleSubmit((data) => {
            onSubmit(data);
          })}
        >
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              gap: "1rem",
            }}
          >
            <div className="form-control" style={{ flex: 1 }}>
              <label htmlFor="nom">Nom</label>
              <input
                id="nom"
                className={errors.lastName && "error"}
                {...register("lastName", { required: "Nom est requis" })}
                placeholder="Entrer votre Nom"
              />
              {errors.lastName && <small>{errors.lastName.message}</small>}
            </div>

            <div className="form-control" style={{ flex: 1 }}>
              <label htmlFor="prenom">Prenom</label>
              <input
                className={errors.firstName && "error"}
                id="prenom"
                {...register("firstName", { required: "Prenom est requis" })}
                placeholder="Entrer votre prenom"
              />
              {errors.firstName && <small>{errors.firstName.message}</small>}
            </div>
          </div>
          <div className="form-control">
            <label htmlFor="email">Email</label>
            <input
              className={errors.email && "error"}
              id="email"
              {...register("email", {
                required: "Email est requis",
                pattern: {
                  value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                  message: "veuillez fournir un email valide",
                },
              })}
              placeholder="Entrer votre email"
            />
            {errors.email && <small>{errors.email.message}</small>}
          </div>
          <div className="form-control">
            <label htmlFor="entreprise">Entreprise</label>
            <input
              className={errors.entreprise && "error"}
              id="entreprise"
              {...register("entreprise", { required: "Entreprise est requis" })}
              placeholder="Entrer votre entreprise"
            />
            {errors.entreprise && <small>{errors.entreprise.message}</small>}
          </div>
          <div className="form-control">
            <label htmlFor="password">Password</label>
            <input
              className={errors.password && "error"}
              type="password"
              placeholder="Entrer le password"
              id="password"
              {...register("password", {
                required: "Password est requis",
                minLength: {
                  value: 6,
                  message: "veuillez fournir au moins 6 caractères",
                },
              })}
            />

            {errors.password && <small>{errors.password.message}</small>}
          </div>
          <div className="form-control">
            <label htmlFor="cpassword">Confirmer Password</label>
            <input
              className={errors.cpassword && "error"}
              type="password"
              placeholder="Confirmer le password"
              id="cpassword"
              {...register("cpassword", {
                required: "Confirm password est requis",
                minLength: {
                  value: 6,
                  message: "veuillez fournir au moins 6 caractères",
                },
                validate: (value) => value === password.current,
              })}
            />

            {errors.cpassword && <small>{errors.cpassword.message}</small>}
            {errors.cpassword?.type === "validate" && (
              <small>Les mots de passe ne correspondent pas</small>
            )}
          </div>
          <button>Submit</button>
          <p>
            Vous avez déjà un compte?{" "}
            <Link to={login_route}>Connectez-vous ici.</Link>
          </p>
        </form>
      </div>
    </div>
  );
};

export default Register;
